import React from "react";
import { Carta } from "./Carta";

const Tabla = ({ g, tabla, dimension, marcar, marcadas, componible }) => {
  const anchura = Array(dimension).fill(null);

  return (
    <table id="tabla">
      <tbody>
        {anchura.map((_, i) => (
          <tr key={i}>
            {anchura.map((_, j) => {
              const slot = anchura.length * i + j;
              const cartaId = tabla[slot][0];
              return (
                <Carta
                  key={`carta-${slot}`}
                  carta={g.leerCarta(cartaId)}
                  slot={slot}
                  marcar={marcar}
                  marcada={marcadas.includes(slot)}
                  componible={componible}
                />
              );
            })}
          </tr>
        ))}
      </tbody>
    </table>
  );
};

export default Tabla;
